import tabBar from '../../common/tabbar.vue';
import http from '../../common/http.js';
let navId = 0;
let nowpage = 1;
let totalPage = 0;
let isSearch = false;
let interval = null;
// 进入界面的
let isFixedHeight = false;
// 搜索的时候的
let isFixedHeight2 = false;
export default {
	data() {
		return {
			scrollHeight: 0,
			searchKeyword: "",
			navigation: {
				height: 88,
				top: 30,
				left:17,
				title: ""
			},
			iconSize: 20,
			searchText: '',
			articleList: [ 
				
			]
		}
	},
	components: {
		tabBar:tabBar,
	},
	computed: {
		level: function() {
			console.log('userInfo:', this.$store.getters.userInfo);
			if(this.$store.getters.userInfo.level) {
				return this.$store.getters.userInfo.level;
			} else {
				return 0;
			}
		},
		uid: function() {
			if(this.$store.getters.userInfo.uid) {
				return this.$store.getters.userInfo.uid;
			}
			return 0;
		}
	},
	watch:{
		
	},
	onPullDownRefresh() {
		console.log('页面下拉刷新');
		let _self = this;
		isFixedHeight = false;
		_self.getHuashuArticleList();
		setTimeout(function() {				
			uni.stopPullDownRefresh();
		}, 200);
	},
	onLoad(option) {
		console.log('option', option);
		let title = option.title;
		// 获取keyword,
		let key  = option.keyword;
		navId = option.navId;
		if (key) {
			this.searchKeyword = key;
			// 判断从搜索框进入的。
			isSearch = true;
		}
		if(title) {
			title = decodeURIComponent(title);
			uni.setNavigationBarTitle({
				title:title
			});
		}
		let sysinfo = uni.getSystemInfoSync();
		let windowHeight = sysinfo.windowHeight;
		if(getApp().globalData.isIphoneX) {
			windowHeight = windowHeight - 34;
		}
		//this.navigation.height = statusBarHeight;
		let scrollHeight = windowHeight - 50;
		this.scrollHeight = scrollHeight;
		uni.startPullDownRefresh();
	},
	methods: {
		getHuashuArticleList() {
			let keyword = this.searchKeyword;
			console.log('navId', navId);
			let classid = navId;
			let params = {};
			if (keyword) {
				params = { keyword: keyword };
			} else if (classid) {
				params.cid = classid;
			}
			params.uid = this.uid;
			params.nowpage = 1;
			// 搜索框搜索时候,将nowpage 置位1
			nowpage = 1;
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			params.auth = auth;
			let url = apiPrefix + "?mod=loveword&ac=list";
			if(keyword) {
				url = apiPrefix + "?mod=loveword&ac=search";
			}
			params.filterData = true;
			// 通过栏目获取话术列表接口
			http.request(url, params).then(resp=>{
				console.log('resp', resp);
				if(resp.status == 1) {
					let articleData  = resp.data;
					this.articleList =  articleData;
					totalPage   = resp.totalpage;
				}
			});
		},
		searchKeywordFunc() {
			// 调用搜索关键词接口...
			let keyword = this.searchKeyword;
			if(!keyword || keyword.length<2) {
				uni.showToast({
					title: '请输入关键词，且至少2个字符长度!',
					icon:"none",
					duration: 2000
				});
				return;
			}
			isFixedHeight = false;
			let params = {
				filterData:true
			};
			if (keyword) {
				params.keyword = keyword;
			}
			// 表示搜索框的结果...
			isSearch = true;
			params.uid = this.uid;
			params.nowpage = 1;
			this.params = params;
			// 通过关键词获取话术列表接口
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			params.auth = auth;
			const url = apiPrefix + "?mod=loveword&ac=search";
			http.request(url, params).then(resp=>{
				console.log('resp', resp);
				if(resp.status == 1) {
					this.articleList = resp.data;
					this.totalPage   = resp.totalpage;
				} else {
					uni.showToast({
						title: '未查询到结果',
						icon:"none",
						duration: 2000
					});
					return;
				}
			});
		},
		back() {
			uni.navigateBack({
				url:'/pages/index/index',
			});
		},
		upgrade_vip() {
			// 这里跳转到登录界面
			if(this.level<2) {
				uni.navigateTo({
					url: '/pages/user/index'
				});
				return;
			}
			uni.navigateTo({
				url:'/pages/user/upgrade_user_vip'
			});
		},
		copyHuashu(line) {
			let content = line.split('@@')[1];
			console.log('content', content);
			uni.setClipboardData({
				data: content,
				success: function(res) {
					uni.getClipboardData({
						success: function(res) {
							uni.showToast({
								title: '复制成功'
							});
						}
					});
				}
			});
		}
	},
	onReachBottom() {
		console.log("上滑动");
		console.log('reach');
		let _self = this;
		if(isSearch && (!this.searchKeyword || this.searchKeyword.length<2)) {
			// 显示空数据给用户
			return;
		}
		if (nowpage>=totalPage) {
			if (interval) {
				clearTimeout(interval);
			}
			let view = uni.createSelectorQuery().select("#content-view");
			if(!isFixedHeight && !isSearch  && this.uid>0) {
				view.boundingClientRect(data => {
					console.log('data:', data);
					_self.scrollHeight = data.height+20;
					isFixedHeight = true;
				}).exec();
			} else if(!isFixedHeight2 && isSearch && this.uid>0) {
				view.boundingClientRect(data => {
					_self.scrollHeight = data.height+20;
					isFixedHeight2 = true;
				}).exec();
			}
			return;
		}
		uni.showNavigationBarLoading();
		interval = setTimeout(function() {
			nowpage++;
			let params = {};
			params.nowpage = nowpage;
			params.cid = navId;
			params.uid = _self.uid;
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			params.auth = auth;
			let url = '';
			if(isSearch) {
				url = apiPrefix + "?mod=loveword&ac=search";
				params.keyword = _self.searchKeyword;
			} else {
				url = apiPrefix + "?mod=loveword&ac=list";
			}
			params.filterData = true;
			http.request(url, params).then(resp=>{
				console.log('resp', resp);
				if(resp.status == 1) {
					let len = resp.data.length;
					for(let j = 0;j<len;j++) {
						_self.articleList.push(resp.data[j]);
					}
					console.log(_self.articleList);
					totalPage   = resp.totalpage;
				}
			});
			uni.hideNavigationBarLoading();
		}, 200);
		// more = contentdown: "上拉显示更多",
		// loading =contentrefresh: "正在加载...",
		// nomore = contentnomore: "没有更多数据了"
	    // 在此进行上拉刷新的业务逻辑
	}
}
